// import bootstrap from 'bootstrap';
import 'bootstrap/js/dist/alert';
import 'bootstrap/js/dist/dropdown';
import 'bootstrap/js/dist/modal';
import 'bootstrap/js/dist/util';

import datepicker from 'bootstrap-datepicker';
import jQuery from 'jquery';
import popper from 'popper.js';

console.log(datepicker, popper);

jQuery(function() {
  $('#main-form-datepicker.datepicker').datepicker({
    startDate: '-3d',
    templates: {
      leftArrow: '&#8592;',
      rightArrow: '&#8594;',
    },
    container: '#datepicker-wrapper',
  });

  $('#datepicker').datepicker({
    startDate: '-3d',
    templates: {
      leftArrow: '&#8592;',
      rightArrow: '&#8594;',
    },
    container: '.modal-body',
  });

  $('#datepicker').on('changeDate', function() {
    $('#date').val($('#datepicker').datepicker('getFormattedDate'));
  });

  $('.input-group .form-control').focus(function() {
    $(this)
      .closest('.input-group')
      .addClass('in-focus');
  });

  $('.input-group .form-control').focusout(function() {
    $(this)
      .closest('.input-group')
      .removeClass('in-focus');
  });

  $('.dropdown .dropdown-menu').on('click.bs.dropdown', () => {
    $('.dropdown').one('hide.bs.dropdown', function() {
      return false;
    });
  });

  $('.dropdown .dropdown-menu .close').on('click.bs.dropdown', () => {
    $('.dropdown').one('hide.bs.dropdown', function() {
      $(this).removeClass('show');
      $(this)
        .find('.dropdown-menu')
        .removeClass('show');
    });
  });

  $(document).on('show.bs.modal', function(event) {
    if (!event.relatedTarget) {
      $('.modal')
        .not(event.target)
        .modal('hide');
    }
    if ($(event.relatedTarget).parents('.modal').length > 0) {
      $(event.relatedTarget)
        .parents('.modal')
        .modal('hide');
    }
    $('html').addClass('no-scroll');
  });

  $(document).on('shown.bs.modal', function() {
    if ($('body').hasClass('modal-open') == false) {
      $('body').addClass('modal-open');
    }
  });

  $(document).on('hide.bs.modal', function() {
    $('html').removeClass('no-scroll');
  });
});
